
    import {defineComponent, reactive, toRefs} from 'vue';
    import {getTest} from "@/api/test";

    export default defineComponent({
        name: 'Dashboard',
        setup() {
            let data = reactive({
                listData: []
            })
            getTest({
                postId: "1"
            }).then(res => {
                (data.listData as any) = res;
            })
            return {
                ...toRefs(data)
            }
        },
        created() {
        },
        components: {}
    });
